import { Injectable } from '@angular/core';
import { defer, of } from 'rxjs';
import { filter } from 'lodash';
import { CommonService } from './common.service';
import { UtilService } from './util.service';

@Injectable({ providedIn: 'root' })
export class RoutesService {
  constructor(
    private commonService: CommonService,
    private utilService: UtilService
  ) { }

  oprimizeRoute() {
    return this.commonService.get('/optimize');
  }

  getRoutes(ID: string, type: string = null) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          if (type === 'completed') {
            return this.utilService.getStorageData('completedRoutes');
          } else {
            return this.utilService.getStorageData('routes');
          }
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get(`/Routes/${ID}?type=${type}`);
    }
  }

  getStops(routeId: string) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          return this.utilService.getStorageData('stops').then(
            stops => {
              return filter(stops, { routeId });
            });
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get('/Stops/' + routeId);
    }
  }

  getStopsDetails(stopId: string) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          return this.utilService.getStorageData('documentinfo').then(
            documentinfo => {
              return filter(documentinfo, { XDOCNUM_0: stopId });
            });
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get('/Documentinfo/' + stopId)
    }
  }

  getDocumentDetails(detailDetId: string) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          return this.utilService.getStorageData('documentdet').then(
            documentdet => {
              return filter(documentdet, { XDOCNUM_0: detailDetId });
            });
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get('/DocumentDet/' + detailDetId);
    }
  }

  getPod(podId: string) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          return this.utilService.getStorageData('poddet').then(
            poddet => {
              return filter(poddet, { XDOCNUM_0: podId });
            });
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get('/PODDet/' + podId)
    }
  }

  getPodReport(podRepId) {
    if (!navigator.onLine || !this.utilService.isOnline) {
      try {
        return defer(() => {
          return this.utilService.getStorageData('podrep').then(
            podrep => {
              return filter(podrep, { XDOCNUM_0: podRepId });
            });
        });
      } catch (error) {
        return of([]);
      }
    } else {
      return this.commonService.get('/PODRep/' + podRepId);
    }
  }

  getStartRoute(startRouteId: any) {
    return this.commonService.post('/DocSta', startRouteId);
  }

  postSkipRoute(params: any) {
    return this.commonService.post('/DocSkip', params);
  }

  postCancelRoute(params: any) {
    return this.commonService.post('/CancelPOD', params);
  }

  postStatus(params: any) {
    return this.commonService.post('/PODStatus', params);
  }

  validateDelivery(params: any) {
    return this.commonService.post('/delivery-validation', params);
  }

  postCheckIn(params: any) {
    return this.commonService.post('/CheckIn', params);
  }

  docReschedule(params: any) {
    return this.commonService.post('/DocRes', params)
  }

  postCheckOut(params: any) {
    return this.commonService.post('/CheckOut', params);
  }

  customerAvailable(params: any) {
    return this.commonService.post('/Custavail', params)
  }

  skipReason() {
    return this.commonService.get('/SkipReasons');
  }

  cancelReasons() {
    return this.commonService.get('/CancelReasons');
  }

  rescheduleReasons() {
    return this.commonService.get('/ReschedReasons');
  }

  incidentReasons() {
    return this.commonService.get('/IncidentReasons');
  }


  getTimeOut() {
    return this.commonService.get('/TimeOut');
  }
}
