import { Injectable, NgZone } from '@angular/core';

import { AlertController, ActionSheetController, LoadingController, ToastController } from '@ionic/angular';

import { Storage } from '@ionic/storage';
import { Router } from '@angular/router';

import { environment } from '../../environments/environment';
import { Camera, CameraDirection, CameraResultType, CameraSource } from '@capacitor/camera';
import { NgxPicaService } from '@digitalascetic/ngx-pica';

@Injectable({
    providedIn: 'root'
})
export class UtilService {

    public static currentPos = {
        lat: 0,
        lng: 0
    };
    public apiUrl: any = environment.host;
    public pictureUrl: any = '';
    // public fileTransfer: FileTransferObject;
    public loader = null;
    public isOnline = true;
    location: any;
    myImage = null;


    constructor(
        public router: Router,
        public alertCtrl: AlertController,
        public actionSheetCtrl: ActionSheetController,
        public loadingCtrl: LoadingController,
        public toastCtrl: ToastController,
        private storage: Storage,
        private ngZone: NgZone,
        private _ngxPicaService: NgxPicaService,
        // public transfer: FileTransfer,
    ) {
        this.pictureUrl = environment.host + 'uploads/';
        // this.fileTransfer = this.transfer.create();
    }



    setOnline(flag) {
        this.isOnline = flag;
    }

    async showToast(message: string = 'Something went wrong. Please try again later.') {
        const toast = await this.toastCtrl.create({
            message,
            duration: 3000
        });
        toast.present();
    }

    async showAlert(title, subTitle, message, callback?: any, buttonText = 'Ok') {
        const alert = await this.alertCtrl.create({
            header: title,
            subHeader: subTitle,
            message,
            backdropDismiss: false,
            buttons: [{
                text: buttonText,
                role: 'cancel',
                handler: (blah) => {
                    if (callback) { callback(); }
                }
            }]
        });

        await alert.present();
    }

    async showConfirmationPopup(title, msg, callback, buttonText = ['Yes', 'No']) {
        const alert = await this.alertCtrl.create({
            header: title,
            message: msg,
            buttons: [
                {
                    text: buttonText[1],
                    role: 'cancel',
                    handler: (blah) => {
                        callback(0);
                    }
                }, {
                    text: buttonText[0],
                    handler: () => {
                        callback(1);
                    }
                }
            ]
        });

        await alert.present();
    }

    async showLoading(message: string = 'Please wait') {
        await this.loadingCtrl.create({
            message,
            animated: true
        }).then(loader => {
            this.loader = loader;
            this.loader.present();
        });
    }
    async showToastSucccess(message: string = "") {
        const toast = await this.toastCtrl.create({
            message,
            duration: 3000,
            color: "success",
            position: "top",
        });
        toast.present();
    }
    async showToastError(message: string = "") {
        const toast = await this.toastCtrl.create({
            message,
            duration: 3000,
            color: "danger",
        });
        toast.present();
    }
    async dismissLoading() {
        if (this.loader) {
            this.loader.dismiss();
        }
    }

    async showImageActionSheet(isDocSelection, callback) {
        const options: any = [
            {
                text: 'Load From Library',
                handler: () => {
                    callback(CameraSource.Photos);
                }
            },
            {
                text: 'Use Camera',
                handler: () => {
                    callback(CameraSource.Camera);
                }
            },

        ];

        // if (isDocSelection) {
        //   options.push({
        //     text: 'Select Document',
        //     handler: () => {
        //       callback(3);
        //     }
        //   });
        // }

        options.push({
            text: 'Cancel',
            role: 'cancel'
        });

        const actionSheet = await this.actionSheetCtrl.create({
            header: 'Select Source',
            buttons: options
        });
        await actionSheet.present();
    }

    pickCamera(type, callback) {
        if (type === CameraSource.Camera) {
            const cameraInput = document.getElementById('cameraInput');
            cameraInput.click();
            cameraInput.onchange = async (ev) => {
                if (ev.target['files'][0]) {
                    this.resizeImage(ev.target['files'][0], callback);
                }
            };
        } else {
            Camera.getPhoto({
                quality: 100,
                allowEditing: true,
                resultType: CameraResultType.DataUrl,
                source: type,
            })
                .then((ImageData) => {
                    const file = this.dataURItoBlob(ImageData.dataUrl);
                    this.resizeImage(file, callback);
                    // callback(ImageData.dataUrl.split(',')[1]);
                })
                .catch((err) => { 
                });
        }
    }

    resizeImage(file, callback?) {
        this._ngxPicaService.resizeImage(file as any, 300, 300, { aspectRatio: { keepAspectRatio: true }, quality: 0.3, exifOptions: { forceExifOrientation: false } })
            .subscribe(async res => {
                const base64: any = await this.toBase64(res);
                callback(base64.split(',')[1]);
            });
    }

    toBase64 = (file) =>
        new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = reject;
        });

    public showErrorCall(err, showErrToast: boolean = false, errMsg?: string) {
        if (err.status === 401) {
            this.logout();
        }
        if (err.error) {
            try {
                const errBody = err.error;
                errMsg = errBody.message;
                if (typeof errMsg === 'object') {
                    let errTypes: any = [];
                    Object.keys(errMsg).forEach(key => {
                        errTypes = errTypes.concat(errMsg[key]);
                    });
                    errMsg = errTypes.join(',');
                }
            } catch (error) {
                this.showToast();
            }
        } else {
            this.showToast();
        }

        if (errMsg) {
            this.showToast(errMsg);
        } else {
            this.showToast(errMsg);
        }
    }


    logout() {
        this.showAlert('Alert', 'Your session is expired due to inactive use.', 'Please tap Login to continue.', () => {
            localStorage.clear();
            this.storage.clear();
            this.router.navigate(['/login'], { replaceUrl: true });
            window.dispatchEvent(new CustomEvent('user:logout'));
        }, 'Login');
    }

    dataURItoBlob(dataURI) {
        // convert base64/URLEncoded data component to raw binary data held in a string
        let byteString;
        if (dataURI.split(',')[0].indexOf('base64') >= 0) {
            byteString = atob(dataURI.split(',')[1]);
        } else { byteString = unescape(dataURI.split(',')[1]); }

        // separate out the mime component
        const mimeString = dataURI
            .split(',')[0]
            .split(':')[1]
            .split(';')[0];

        // write the bytes of the string to a typed array
        const ia = new Uint8Array(byteString.length);
        for (let i = 0; i < byteString.length; i++) {
            ia[i] = byteString.charCodeAt(i);
        }

        return new Blob([ia], { type: mimeString });
    }

    setStorageData(key, value) {
        return this.storage.set(key, value);
    }

    getStorageData(key) {
        return this.storage.get(key);
    }


    public formatNumber(value): string {
        if (!value) {
            return '';
        }
        const regex = /[-+*/()=,\s]/g;
        value = value.replace(regex, '');
        const parts = value.toString().split('.');
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ',');
        return parts.join('.');
    }

    redirectToBrowser(link) {
        window.open(link, '_system');
    }
}
